body {
    height: 5000px;
}

/* Video Slider Section - Start */
.video-background-holder {
    position: relative;
    background-color: #000;
    height: calc(100vh - 320px);
    min-height: 41rem;
    width: 100%;
    overflow: hidden;
    .video-background-overlay {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: #000;
        opacity: .5;
        z-index: 1;
    }
    .video-background-holder video {
        position: absolute;
        top: 50%;
        left: 70%;
        min-width: 100%;
        min-height: 100%;
        width: auto;
        height: auto;
        z-index: 0;
        transform: translateX(-50%) translateY(-50%);
    }
    .video-background-content {
        position: absolute;
        top: 0;
        left: 0;
    }
}

/* share-list - start
-------------------------------------------------- */
.share-list {
    width: auto;
    position: relative;
  }
  .share-list .share-link {
    top: 0;
    opacity: 0;
    z-index: -1;
    width: 40px;
    height: 40px;
    right: -14px;
    font-size: 18px;
    line-height: 40px;
    text-align: center;
    position: absolute;
  
    transform: scale(0);
    -o-transform: scale(0);
    -ms-transform: scale(0);
    -moz-transform: scale(0);
    -webkit-transform: scale(0);
  
    transition: all .3s ease-in-out;
    -o-transition: all .3s ease-in-out;
    -ms-transition: all .3s ease-in-out;
    -moz-transition: all .3s ease-in-out;
    -webkit-transition: all .3s ease-in-out;
  }
  .share-list .share-link a {
    width: 35px;
    height: 35px;
    color: #050505;
    line-height: 35px;
    text-align: center;
    border-radius: 3px;
    background-color: #ffa025;
  }
  .share-list .share-link a:hover {
    color: #ffffff;
    background-color: #1455a7;
  }
  
  .share-list:hover .share-link.facebook {top: -200px;transition-delay: .5s;}
  .share-list:hover .share-link.twitter {top: -160px;transition-delay: .4s;}
  .share-list:hover .share-link.googleplus {top: -120px;transition-delay: .3s;}
  .share-list:hover .share-link.linkedin {top: -80px;transition-delay: .2s;}
  .share-list:hover .share-link.dribbble {top: -40px;transition-delay: .1s;}
  
  
  .share-list:hover .share-link.facebook,
  .share-list:hover .share-link.twitter,
  .share-list:hover .share-link.googleplus,
  .share-list:hover .share-link.linkedin,
  .share-list:hover .share-link.dribbble {
    z-index: 1;
    opacity: 1;
  
    transform: scale(1);
    -o-transform: scale(1);
    -ms-transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
  }
  /* share-list - end
